/****** LOGIN MODAL ******/
.loginmodal-container {
    color: black;
    padding: 30px;
    max-width: 350px;
    width: 100% !important;
    margin: 0 auto;
  }
  
  .loginmodal-container h1 {
    text-align: center;
    font-size: 1.8em;
  }
  
  .loginmodal-container input[type=submit] {
    width: 100%;
    display: block;
    margin-bottom: 10px;
    position: relative;
  }
  .loginmodal-container input[type=text] {
    margin-bottom: 20px;
  }
  .loginmodal-container input[type=text], input[type=password] {
    border-radius: 90px !important;
    background-color: rgba(255, 255, 255, 0.863) !important; 
    border: none;
    padding: 7px;
    width: 100%;
  }
 
  
  .loginmodal {
    text-align: center;
    font-size: 14px;
    font-family: 'Arial', sans-serif;
    font-weight: 700;
    height: 36px;
    padding: 0 8px;
  }
  
  .loginmodal-submit {
    /* border: 1px solid #3079ed; */
    border: 0px;
    color: #fff;
    text-shadow: 0 1px rgba(0,0,0,0.1); 
    background-color: #4d90fe;
    padding: 17px 0px;
    font-size: 14px;
  }
  
  .loginmodal-submit:hover {
    /* border: 1px solid #2f5bb7; */
    border: 0px;
    text-shadow: 0 1px rgba(0,0,0,0.3);
    background-color: #357ae8;
  }
  
  .loginmodal-container a {
    text-decoration: none;
    font-size: 17px;
    color: #fff;
    font-weight: 400;
    text-align: center;
    display: inline-block;
  } 
  .loginmodal-container a:hover {
    color: #1a1a1a;
    text-decoration: none;
  } 
  
  .login-help{
    font-size: 14px;
  }
  .login-help a{
    color: red;
  }
  
  .content {
    padding-top: 30px;
  }
  .loginWrap h1 {
    text-transform: uppercase;
    padding: 7px;
    background: rgba(255, 255, 255, 0.781);
    color: rgba(25, 149, 251, .80);
    font-weight: 700;
    font-size: 40px;
    margin: 0 auto 26px;
  }
  .loginWrap {
    padding-top: 130px;
  }

  .loginmodal-submit {
    color: #fff!important;
    background: #1995fb; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #1995fb 0%,#0079db 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1995fb', endColorstr='#0079db',GradientType=0 ); /* IE6-9 */
    font-weight: 700!important;
    text-transform: uppercase!important;
    font-size: 30px!important;
    border: solid 5px #fff!important;
    padding: 0px 40px!important;
    border-radius: 90px!important;
    display: block!important;
    margin: 23px auto 0px;
  }
  .loginmodal-submit:hover {
    background: #0079db; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #0079db 0%,#1995fb 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0079db', endColorstr='#1995fb',GradientType=0 ); /* IE6-9 */
  }


  /* media query */

  @media (max-width: 1199.98px) {

  }
  @media (max-width: 991.98px) {
      
  }
  @media (max-width: 767.98px) {
      
  }
  @media (max-width: 575.98px) {
      .loginWrap h1 {
          font-size: 30px;
      }
      .loginWrap {
          padding-top: 80px;
      }
      .loginmodal-submit {
          font-size: 24px!important;
      }
  }

  /* Login CSS End */
.btn-primary {
    color: #fff!important;
    background: #1995fb; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #1995fb 0%,#0079db 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1995fb', endColorstr='#0079db',GradientType=0 ); /* IE6-9 */
    font-weight: 700!important;
    text-transform: uppercase!important;
    font-size: 30px!important;
    border: solid 5px #fff!important;
    padding: 0px 40px!important;
    border-radius: 90px!important;
    display: block!important;
    margin: 23px auto 0px;
}
.btn-primary:hover {
    background: #0079db; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #0079db 0%,#1995fb 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0079db', endColorstr='#1995fb',GradientType=0 ); /* IE6-9 */
}
